	module.exports = {"data":{"content":{"content":"<h2 id=前言>前言</h2>\n<p>與其說黑客馬拉松是一場比賽，不如說它是一場程序員的盛會。作為一個前端開發者，也是蠻喜歡參加這樣的比賽。畢竟可以把自己的想法，在 24 小時之內把自己的想法，或是按著比賽主題做個原型出來。無論你是什麼技術背景的人，都歡迎你來參與這些盛會。</p>\n<h2 id=為什麼我會開始參加黑客馬拉松？>為什麼我會開始參加黑客馬拉松？</h2>\n<p>一開始，我沒有想那麼多。無論是取得勝利，或者是獲取經驗，都能夠幫助我成長。如果是取得名次的話，那就可以跟人家分享一下喜悅。如果是取得經驗的話，那也能從一個短時間試驗自己對於不同技術的掌握程度。無論如何，有好吃的東西是少不了www</p>\n<p>另外一個我喜歡黑客松的的地方，就是比賽的獎品也是非常豐富的。ImagineHack 為我帶來的除了第三名的名次之外，還有 FitBit 這一個智能手環。而深圳 TechCrunch 2017 黑客松，則是為我帶來現在我寫代碼，或者是寫文章必備的 HHKB，詳見<a href=\"/2017/07/02/introduction-to-hhkb-pro-2/\">《神兵利器 -- HHKB Pro 2 開箱文》</a>一文。現在無論是寫代碼還是寫文章，我都不需要看著鍵盤，就可以自由地輸入。</p>\n<h2 id=比賽內容>比賽內容</h2>\n<p><a data-fancybox=\"gallery\" href=\"https://i.imgur.com/IJPUhSJh.jpg\"><img class=\"lozad d-block mx-auto\" data-src=https://i.imgur.com/IJPUhSJl.jpg title=\"hackUST 2017\" /></a></p>\n<p>比賽通常都是持續 24 小時，甚至更加長的時間的。比如說第一天的早上開始，到第二天的下午結束。如果那次比賽舉辦單位有名氣的話，或者是比較願意投放資源到福利上面，那麼我們吃的食物也是比較豪華的。當中不乏自助餐，要兩個人伸手才能圍著的超級大 Pizza，還有很多零食，飲料。</p>\n<h2 id=報名方法>報名方法</h2>\n<p>這些比賽大部分都是免費的，有一些舉辦單位可能會收取幾十塊錢，但你依然可以從不同途徑獲取免費門票。所以如果你沒有參加過的話，你也可以選擇參加一些免費的黑客馬拉松，體驗一下。</p>\n<p>要找到這些黑客馬拉松，一點也不難。你只需要留意不同社群的信息，就會發現幾乎每個月都有黑客松。這些舉辦單位大多鼓勵不同的參賽者參加，也就是不同的人也能夠參加比賽。比如說程序員，設計師，還有一些商業背景的人也會參加。無論你是否一個程序員，你都可以發揮你的想象力，可能你就是下一個贏家。</p>\n<h2 id=感想>感想</h2>\n<p>這麼有趣的活動，能夠和不同人交流一下，也能驗證自己的技術水平。一舉兩得的事情，何樂而不為呢？ <strong>Just do IT</strong>。有興趣的話，可以看一下我寫的兩篇後記：<a href=\"2017/04/23/hackUST-2017-hackathon-summary/\">hackUST 2017 後記</a>，<a href=\"/2017/02/19/supercharger-hackathon\">SuperCharger Hackathon 後記</a>。希望在下一次比賽可以有合作的機會。</p>\n","title":"參加八次黑客馬拉松的個人體會與總結","createdDate":"2017-11-06T19:32+08:00","headerImgur":"qNs8pq4.jpg","id":"6ZLyndur7OAASguoo0csY4","toc":["前言","為什麼我會開始參加黑客馬拉松？","比賽內容","報名方法","感想"],"tags":"前端, 閱讀, Hackathon"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"6ZLyndur7OAASguoo0csY4"}}

